.flowchart-render{position:relative;border:1px dashed #ddd;overflow:hidden}.flowchart-render canvas{position:absolute;width:100%;height:100%;top:0;left:0;z-index:99}.flowchart-render .block{position:absolute;z-index:999;border:1px solid #333}.flowchart-render .block .name{text-align:center;padding-top:30%}


